<template>
  <div class="NumberMemory">
    <van-grid :column-num="col" :gutter="8" :clickable="true">
      <van-grid-item v-for="i in n" :key="i" :text="'' + displayArr[i - 1]" @click="jump(i - 1)"/>
    </van-grid>
    <van-popup safe-area-inset-bottom
               v-model="showButton" :overlay="false" duration="0" position="bottom">
      <van-button type="primary" @click="startToAnswer">记住了</van-button>
      <van-divider/>
    </van-popup>
    <van-popup safe-area-inset-bottom
               v-model="showKey" :overlay="false" duration="0" position="bottom">
      <van-grid :column-num="col" :gutter="8" :clickable="true">
        <van-grid-item v-for="i in n" :key="i" :text="'' + keyArr[i - 1]" @click="fill(keyArr[i - 1])"/>
      </van-grid>
      <van-divider/>
    </van-popup>
    <van-popup safe-area-inset-bottom
               duration="0" :close-on-click-overlay="false" :closeable="true" @closed="Refresh"
               v-model="showScore" position="bottom">
      <pre class="game_formula" style="font-size: 30px;">{{ score }}</pre>
      <pre class="game_formula" style="margin-top: 0;font-size: 20px;">{{ scoreEn }}</pre>
    </van-popup>
    <Start></Start>
  </div>
</template>

<script>
const Start = () => import('@/components/Start.vue')

import {timeFormatCn, timeFormatEn} from "@/util/timeFormat"

import MtaH5 from 'mta-h5-analysis';

export default {
  components: {
    Start,
  },
  data() {
    return {
      displayArr: [],
      answerArr: [],
      keyArr: [],
      rightCount: 0,
      wrongCount: 0,
      selectIndex: 0,
      startTime: new Date().getTime(),
      midTime: new Date().getTime(),
      showButton: true,
      showKey: false,
      showScore: false,
      score: '',
      scoreEn: '',
      repeat: this.$route.query.repeat === undefined ? false : this.$route.query.repeat === "true",
      row: this.$route.query.row === undefined ? 5 : parseInt(String(this.$route.query.row)),
      col: this.$route.query.col === undefined ? 5 : parseInt(String(this.$route.query.col)),
      n: 0,
    };
  },
  created() {
    this.n = this.row * this.col;
    let set = {};
    let num = 10;
    for (let i = 0; i < this.n; i++) {
      this.$set(this.keyArr, i, i + 1);
      do {
        num = Math.ceil(Math.random() * this.n);
      } while (set[num])
      set[num] = 1;
      this.$set(this.displayArr, i, num);
      this.$set(this.answerArr, i, num);
    }
  },
  mounted() {
    let _this = this;
    document.onkeydown = function (e) {
      let key = e.key;
      if (key >= 0 && key <= 9) {
        _this.onInput(key);
      } else {
        _this.onDelete();
      }
    };
  },
  methods: {
    startToAnswer() {
      this.showButton = false;
      this.showKey = true;
      this.$set(this.displayArr, 0, "__");
      for (let i = 1; i < this.n; i++) {
        this.$set(this.displayArr, i, "　");
      }
      this.midTime = new Date().getTime()
    },
    jump(selectIndex) {
      if (this.displayArr[selectIndex] !== "　") {
        return
      }
      if (this.displayArr[this.selectIndex] === "__") {
        this.$set(this.displayArr, this.selectIndex, "　");
      }
      this.$set(this.displayArr, selectIndex, "__");
      this.selectIndex = selectIndex;
    },
    fill(answerNum) {
      if (this.displayArr[this.selectIndex] !== "__") {
        console.log("不会出现的情况")
        return;
      }
      if (answerNum === "　") {
        return;
      }
      if (this.answerArr[this.selectIndex] === answerNum) {
        this.$set(this.displayArr, this.selectIndex, answerNum);
        this.rightCount++;
      } else {
        this.$set(this.displayArr, this.selectIndex, "(" + this.answerArr[this.selectIndex] + ")");
        this.wrongCount++;
      }
      if (!this.repeat) {
        this.keyArr[this.answerArr[this.selectIndex] - 1] = "　"
      }
      if ((this.rightCount + this.wrongCount) === this.n) {
        // 减去倒计时的 3 秒
        let memoryTime = this.midTime - this.startTime - 3000;
        let answerTime = new Date().getTime() - this.midTime;
        let memoryTimeCn = timeFormatCn(memoryTime);
        let answerTimeCn = timeFormatCn(answerTime);
        this.score = this.n + ' 个数字记住 ' + this.rightCount + ' 个' +
            '\n记忆时长 ' + memoryTimeCn +
            '\n填写时长 ' + answerTimeCn;
        this.scoreEn = this.n + ' number remember ' + this.rightCount +
            '\nmemory time: ' + timeFormatEn(memoryTime) +
            '\nanswer time: ' + timeFormatEn(answerTime);
        this.showScore = true;
        MtaH5.clickStat('NumberMemoryScore', {
          'n': this.n,
          'rightcount': this.rightCount,
          'memorytime': memoryTime,
          'answertime': answerTime,
          'memorytimecn': memoryTimeCn,
          'answertimecn': answerTimeCn,
        })
        return;
      }
      do {
        this.selectIndex++;
        if (this.displayArr[this.selectIndex] === undefined) {
          this.selectIndex = 0;
        }
      } while (this.displayArr[this.selectIndex] !== "　");
      this.jump(this.selectIndex);
    },
    Refresh() {
      this.$router.replace('/Refresh');
    },
  },
};
</script>

<style lang="css" scoped>
.NumberMemory >>> .van-grid-item__text {
  font-size: 30px;
  font-weight: bold;
}

.NumberMemory >>> .van-grid-item__content {
  padding: 0;
}
</style>